import React, { useState } from 'react';
import dayjs from 'dayjs';
import { DemoContainer } from '@mui/x-date-pickers/internals/demo';
import { LocalizationProvider, DatePicker } from '@mui/x-date-pickers-pro';
import { AdapterDayjs } from '@mui/x-date-pickers-pro/AdapterDayjs';
import 'dayjs/locale/en-gb';

export default function Calendar({ startDate, endDate, onDateChange }) {
  const currentDate = dayjs();

  const [selectedStartDate, setSelectedStartDate] = useState(startDate);
  const [selectedEndDate, setSelectedEndDate] = useState(endDate);

  const handleStartDateChange = (newDate) => {
    if (newDate.isBefore(selectedEndDate) || newDate.isSame(selectedEndDate, 'day')) {
      setSelectedStartDate(newDate);
      onDateChange([newDate, selectedEndDate]);
    }
  };

  const handleEndDateChange = (newDate) => {
    if (newDate.isAfter(selectedStartDate) || newDate.isSame(selectedStartDate, 'day')) {
      setSelectedEndDate(newDate);
      onDateChange([selectedStartDate, newDate]);
    }
  };

  return (
    <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="en-gb">
      <DemoContainer components={['DateRangePicker']}>
        <DatePicker
          label="Bought on"
          sx={{ width: 300 }}
          value={selectedStartDate}
          onChange={handleStartDateChange}
          textField={(startProps) => (
            <div>
              <input {...startProps.inputProps} selectedStartDate readOnly />
              {startProps.startAdornment}
            </div>
          )}
          maxDate={selectedEndDate.subtract(1, 'day')} 
        />
        <DatePicker
          label="Held to"
          sx={{ width: 300 }}
          value={selectedEndDate}
          onChange={handleEndDateChange}
          textField={(endProps) => (
            <div>
              <input {...endProps.inputProps} selectedEndDate readOnly />
              {endProps.startAdornment}
            </div>
          )}
          minDate={selectedStartDate.add(1, 'day')} 
          maxDate={currentDate}
        />
      </DemoContainer>
    </LocalizationProvider>
  );
}
