import SearchBar from "./components/SearchBar";
import Calendar from "./components/Calendar";
import Header from "./components/Header";
import ButtonComponent from "./components/ButtonComponent";
import ResultModal from "./components/ResultModal";
import React, { useState, useEffect } from "react";
import dayjs from 'dayjs';
import axios from "axios";

function App() {
  const [startDate, setStartDate] = useState(dayjs().subtract(1, 'month'));
  const [endDate, setEndDate] = useState(dayjs());
  const [coins, setCoins] = useState([])
  const [selectedCoin, setSelectedCoin] = useState(null);
  const [startPrice, setStartPrice] = useState(null);
  const [endPrice, setEndPrice] = useState(null);

  const url = 'https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false&locale=en&precision=3';
  
  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(url);
        setCoins(response.data);
      } catch (error) {
        console.log(error);
      }
    };
    fetchData();
  }, []);

  const handleDateChange = (newDateRange) => {
    setStartDate(newDateRange[0]);
    setEndDate(newDateRange[1]);
  }

  const handleCoinSelect = (coin) => {
    setSelectedCoin(coin);
  };

  const formatDateForAPI = (date) => date.format('DD-MM-YYYY');

  const handleButtonClick = async (selectedStartDate, selectedEndDate) => {
    
    if (selectedCoin) {
      const startTimestamp = formatDateForAPI(selectedStartDate);
      const endTimestamp = formatDateForAPI(selectedEndDate);

      const startApiUrl = `https://api.coingecko.com/api/v3/coins/${selectedCoin.toLowerCase()}/history?date=${startTimestamp}&localization=false`;
      const endApiUrl = `https://api.coingecko.com/api/v3/coins/${selectedCoin.toLowerCase()}/history?date=${endTimestamp}&localization=false`;

      console.log('Start API URL:', startApiUrl); // Check API URL
      console.log('End API URL:', endApiUrl); // Check API URL

      try {
        const startResponse = await axios.get(startApiUrl);
        const endResponse = await axios.get(endApiUrl);

        console.log('Start Response:', startResponse.data); // Check API response
        console.log('End Response:', endResponse.data); // Check API response

        setStartPrice(startResponse.data.market_data.current_price.usd);
        setEndPrice(endResponse.data.market_data.current_price.usd);
      } catch (error) {
        console.log('Error fetching historical price data:', error);
      }
    }
  };

  const [isModalOpen, setIsModalOpen] = useState(false); 

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };


  return (
    <div className="App">
      <Header />
      <SearchBar coins={coins} onCoinSelect={handleCoinSelect} />
      <Calendar startDate={startDate} endDate={endDate} onDateChange={handleDateChange} onButtonClick={handleButtonClick} />
      <ButtonComponent 
        selectedCoin={selectedCoin} 
        startDate={startDate}
        endDate={endDate}
        onButtonClick={() => {
          handleButtonClick(startDate, endDate);
          openModal(); 
        }} 
      />
      {isModalOpen && (
        <ResultModal
          startPrice={startPrice}
          endPrice={endPrice}
          closeModal={closeModal}
        />
      )}
    </div>
  );
}

export default App;