import React from 'react';
import { Button as MUIButton }  from '@mui/material';
import axios from 'axios';
import dayjs from 'dayjs';

export default function ButtonComponent({ selectedCoin, startDate, endDate, onButtonClick }) {
  const handleButtonClick = async () => {
    if (selectedCoin) {
      try {
        const coinId = selectedCoin.toLowerCase(); 
        const fromTimestamp = startDate.unix(); 
        const toTimestamp = endDate.unix(); 

        const historicalPriceUrl = `https://api.coingecko.com/api/v3/coins/${coinId}/market_chart/range?vs_currency=usd&from=${fromTimestamp}&to=${toTimestamp}`;

        const response = await axios.get(historicalPriceUrl);
        const historicalPriceData = response.data.prices;

        const midnightPrices = historicalPriceData.filter(priceData => {
          const timestamp = priceData[0];
          const date = dayjs.unix(timestamp);
          return date.isSame(date.startOf('day'), 'second'); 
        });

        console.log('Historical Price Data at Midnight:', midnightPrices);
        console.log('Selected Start Date:', fromTimestamp);
        console.log('Selected End Date:', toTimestamp);
        onButtonClick(startDate, endDate);
      } catch (error) {
        console.error('Error fetching historical price data:', error);
      }
    }
  };

  return (
    <MUIButton variant="contained" onClick={handleButtonClick}>
      Submit
    </MUIButton>
  );
}

