import Modal from '@mui/material/Modal';

export default function ResultModal({ startPrice, endPrice, closeModal }) {
  const priceRatio = endPrice / startPrice;
  let backgroundColor = "#ffffff"; 
  
  if (priceRatio < 0.8) {
    backgroundColor = "#e60000";
  } else if (priceRatio >= 0.8 && priceRatio < 1) {
    backgroundColor = "#ed8105";
  } else if (priceRatio > 1 && priceRatio < 1.2) {
    backgroundColor = "#8fce00";
  } else if (priceRatio >= 1.2) {
    backgroundColor = "#6aa84f";
  } else {
    backgroundColor = "#ffffff";
  }

  const modalStyle = {
    backgroundColor: backgroundColor,
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    borderRadius: '1rem', 
    boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)', 
    padding: '1rem 2rem',
    display: 'flex',
    alignItems: 'center',
    fontWeight: 600,
    flexDirection: 'column',
    width: "400px",
    maxWidth: "90vw",
  };

    return (
      <Modal
        open={true}
        onClose={closeModal}
        aria-labelledby="modal-title"
        aria-describedby="modal-description"
      >
        <div style={modalStyle}>
          <h2 id="modal-title">Investment specifics</h2>
          <div className="modal-description">
          <p className='price-description'>Start Price:</p>
          <p className='modal-price'>{parseFloat(startPrice).toFixed(3)}</p>
          </div>
          <div className="modal-description">
          <p className='price-description'>End Price:</p>
          <p className='modal-price'>{parseFloat(endPrice).toFixed(3)}</p>
          </div>
          <div id="roi-container">
            <p>Your ROI:</p>
            <p>{parseFloat(priceRatio).toFixed(3)}x</p>
          </div>
        </div>
      </Modal>
    );
  }
  