import React from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';

export default function SearchBar({ coins, onCoinSelect }) {
  const handleCoinSelect = (coin) => {
    onCoinSelect(coin); 
  };

  return (
    <Autocomplete
      disablePortal
      id="combo-box-demo"
      options={coins.map((coin) => coin.name)}
      sx={{ width: 300 }}
      onChange={(_, coin) => handleCoinSelect(coin)}
      renderInput={(params) => <TextField {...params} label="Coin" />}
    />
  );
}
